.is-progress-holder {
    position: fixed;
    background: rgba(255, 255, 255, 0.84);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;

    .progress {
      width: 50%; } }

.progress {
  @include block;
  -moz-appearance: none;
  -webkit-appearance: none;
  border: none;
  border-radius: 290486px;
  display: block;
  height: $size-normal;
  overflow: hidden;
  padding: 0;
  width: 100%;
  &::-webkit-progress-bar {
    background-color: $border; }
  &::-webkit-progress-value {
    background-color: $text; }
  &::-moz-progress-bar {
    background-color: $text; }
  // Colors
  @each $name, $pair in $colors {
    $color: nth($pair, 1);
    &.is-#{$name} {
      &::-webkit-progress-value {
        background-color: $color; }
      &::-moz-progress-bar {
        background-color: $color; } } }
  // Sizes
  &.is-small {
    height: $size-small; }
  &.is-medium {
    height: $size-medium; }
  &.is-large {
    height: $size-large; } }
